<template>
  <div class="page-container">
    <!--   LoadingSpinner -->
    <LoadingSpinner :visible="isLoading" />

    <!--  -->
    <div class="header">
      <img src="../assets/header_image.jpg" alt="Header Image" />
      <div class="header-content">
        <div class="left">
          <h1><br /></h1>
          <p>Discover the Power of Partnership for<br />Sustainable Water Solutions</p>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="admin">
      <button class="admin-login-button" @click="openLoginModal"></button>
    </div>

    <!--  -->
    <div v-if="isLoginModalVisible" class="login-modal">
      <div class="modal-content">
        <LoginForm @login-success="handleLoginSuccess" />
        <button class="login-close-button" @click="closeLoginModal"></button>
      </div>
    </div>

    <div class="description">
      
    </div>

    <!--  -->
    <div class="status-row">
      <div
        class="status-box"
        v-for="(label, index) in labels"
        :key="index"
        :class="{ clickable: label === '' || label === '' }"
        @click="handleStatusBoxClick(label)"
      >
        <h3>{{ currentMonth }}</h3>
        <p class="status-title">{{ label }}</p>
        <p class="status-number">
          {{
            label === ''
              ? listedCompanyCount
              : label === ''
              ? nonListedCompanyCount
              : ''
          }} 
        </p>
        <div class="status-note"></div>
      </div>
    </div>

    <!--  -->
    <div class="table-header-container">
      <div class="table-header">
        <i class="fas fa-list"></i> <span></span>
      </div>
    </div>

    <!--  -->
    <div class="grid-container">
      <!--  1 -->
      <div class="grid-item">
        <div class="table-wrapper">
          <ChartSpinner :visible="isFirstTableLoading" />
          <table>
            <tr><th colspan="2"></th></tr>
            <tr v-for="(company, index) in listedCompanies" :key="index">
              <td>{{ company.company_name }}</td>
            </tr>
            <tr v-if="firstTablePages > 1">
              <td colspan="2">
                <div class="pagination">
                  <button :disabled="firstTablePage === 1" @click="changePage('firstTable', firstTablePage - 1)"></button>
                  <span>{{ firstTablePage }} / {{ firstTablePages }}</span>
                  <button :disabled="firstTablePage === firstTablePages" @click="changePage('firstTable', firstTablePage + 1)"></button>
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>

      <!--  2 -->
      <div class="grid-item">
        <div class="table-wrapper">
          <ChartSpinner :visible="isSecondTableLoading" />
          <table>
            <tr><th colspan="2"></th></tr>
            <tr v-for="(company, index) in nonListedCompanies" :key="index">
              <td>{{ company.company_name }}</td>
            </tr>
            <tr v-if="secondTablePages > 1">
              <td colspan="2">
                <div class="pagination">
                  <button :disabled="secondTablePage === 1" @click="changePage('secondTable', secondTablePage - 1)"></button>
                  <span>{{ secondTablePage }} / {{ secondTablePages }}</span>
                  <button :disabled="secondTablePage === secondTablePages" @click="changePage('secondTable', secondTablePage + 1)"></button>
                </div>
              </td>
            </tr>
          </table>
        </div>
      </div>

      <!--  3 -->
      <div class="grid-item">
        <table>
          <tr><th colspan="2"></th></tr>
          <tr><td>20253</td></tr>
          <tr><td>202412</td></tr>
        </table>
      </div>
    </div>

    <!--  -->
    <div class="footer">
      <div class="footer-overlay"></div>
      <img class="footer-bg" src="@/assets/footer_image.jpg" alt="Footer Image" />
      <div class="footer-content">
        <a href=""></a>
        <a href="">/</a>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import LoginForm from '@/components/common/LoginForm.vue';
import LoadingSpinner from '@/components/common/LoadingSpinner.vue';
import ChartSpinner from '@/components/common/ChartSpinner.vue';

export default {
  name: 'App',
  components: {
    LoginForm,
    LoadingSpinner,
    ChartSpinner
  },
  data() {
    return {
      isLoading: true, //  loading
      isFirstTableLoading: false, //  1  loading
      isSecondTableLoading: false, //  2  loading
      currentMonth: '', // 
      labels: ['', '', ''],
      listedCompanyCount: 0,
      listedCompanies: [],
      nonListedCompanyCount: 0,
      nonListedCompanies: [],
      firstTablePage: 1,
      firstTablePages: 1,
      secondTablePage: 1,
      secondTablePages: 1,
      thirdTablePage: 1,
      thirdTablePages: 1,
      isLoginModalVisible: false
    };
  },
  mounted() {
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, '0');
    this.currentMonth = `${year}${month}`;
    this.loadAllData();
  },
  methods: {
    // 
    async loadAllData() {
      this.isLoading = true;
      try {
        const res = await axios.get('/api/company/homepage-summary');
        const data = res.data;
        this.listedCompanyCount = data.listedCompanyCount;
        this.nonListedCompanyCount = data.nonListedCompanyCount;
        this.listedCompanies = data.listedCompanies;
        this.nonListedCompanies = data.nonListedCompanies;
        this.firstTablePages = Math.ceil(data.listedCompanyCount / 10);
        this.secondTablePages = Math.ceil(data.nonListedCompanyCount / 10);
      } catch (err) {
        console.error('', err);
      } finally {
        this.isLoading = false;
      }
    },

    // 
    async changePage(table, page) {
      if (page < 1 || page > this[`${table}Pages`]) return;
      this[`${table}Page`] = page;

      try {
        if (table === 'firstTable') {
          this.isFirstTableLoading = true;
          const res = await axios.get(`/api/company/listed-companies?page=${page}`);
          this.listedCompanies = res.data.data;
        } else if (table === 'secondTable') {
          this.isSecondTableLoading = true;
          const res = await axios.get(`/api/company/non-listed-companies?page=${page}`);
          this.nonListedCompanies = res.data.data;
        }
      } catch (err) {
        console.error(':', err);
      } finally {
        if (table === 'firstTable') this.isFirstTableLoading = false;
        if (table === 'secondTable') this.isSecondTableLoading = false;
      }
    },

    // 
    openLoginModal() {
      this.isLoginModalVisible = true;
    },
    closeLoginModal() {
      this.isLoginModalVisible = false;
    },
    handleLoginSuccess() {
      this.closeLoginModal();
      this.$router.push('/admin-page');
    },

    // 
    handleStatusBoxClick(label) {
      if (label === '') this.$router.push('/dashboard');
      else if (label === '') this.$router.push('/lsdashboard');
    }
  }
};
</script>


