<template>
  <div class="login-form">
    <h2 class="title"></h2>  <!--  -->
    
    <div class="input-group">
      <label for="username"></label>  <!--  -->
      <input v-model="username" type="text" id="username" placeholder="" />
    </div>

    <div class="input-group">
      <label for="password"></label>  <!--  -->
      <input v-model="password" type="password" id="password" placeholder="" />
    </div>

    <button @click="handleLogin"></button>
    <p v-if="errorMessage" style="color: red">{{ errorMessage }}</p>
  </div>
</template>


<script>
import axios from 'axios';

export default {
  name: 'LoginForm',
  data() {
    return {
      username: '',
      password: '',
      errorMessage: ''
    };
  },
  methods: {
    handleLogin() {
      if (!this.username || !this.password) {
        this.errorMessage = '';
        return;
      }
    
      // const apiUrl = process.env.VUE_APP_API_URL || '/api';  //  API 
    
      axios.post('/api/login', {
        username: this.username,
        password: this.password
      }, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => {
        if (response.data.success) {
          localStorage.setItem('adminLoggedIn', 'true');
          this.$emit('login-success'); // 
        } else {
          this.errorMessage = '';
        }
      })
      .catch((error) => {
        console.error(':', error);
        this.errorMessage = '';
      });
    }
  }
};
</script>

<style scoped>
.login-form {
  width: 90%;  /*  */
  max-width: 400px;  /*  400px */
  margin: 0 auto;
  padding: 10px;
  box-sizing: border-box;  /*  */
}

.title {
  font-size: 24px;  /*  */
  color: #003049;  /*  */
  text-align: center;  /*  */
  margin-bottom: 20px;  /*  */
}

.input-group {
  margin-bottom: 20px;  /*  */
}

label {
  font-size: 16px;
  color: #003049;  /*  */
  display: block;
  margin-bottom: 5px;  /*  */
}

input {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  box-sizing: border-box;
}

button {
  width: 100%;
  padding: 12px;
  margin-top: 20px;
  background-color: #003049;  /*  */
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #679898;  /*  */
}
</style>
